
const initialState = {
  api: 'http://localhost:9000',
  googleMapsApiKey: 'AIzaSyB2mzRi4UHxbgUUXOyOjwFVserTD-gDzQc'
}

export default (state = initialState, action) => {
  switch (action.type) {
    case 'INCREMENT_COUNTER':
      return {
        ...state,
        count: state.count + 1,
      }
    case 'DECREMENT_COUNTER':
      return {
        ...state,
        count: state.count - 1,
      }
    default:
      return state
  }
}




